.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
   content: "";
   width: 100%;
   height: 100%;
   position: absolute;
   top: 0;
   left: 0;
   background-color: #000;
   opacity: 0.8;
}
.img{
    height: 200px;
    width: 200px ;
    border-radius: 90%;
}
.hero{
    height: 100%;
    width:100%;
}
.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.hero .content h1{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}
.hero .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}
.content .btn{
    margin: 1rem 0.2rem;
}
@media screen and (max-width: 640px){
    .hero .content h1{
        font-size: 4rem;
    }
    
    .hero .content p{
        font-size: 1.4rem;
    }
    .lean-more{
        background-color: black;
        margin: 1rem 0.2rem;
        display: inline-block;
        padding: 10px 20px;
        font-size: 16px;
        font-weight: bold;
        text-align: center;
        text-decoration: none;
        cursor: pointer;
        border: 2px solid #333;
        border-radius: 5px;
        background-color: #555;
        color: #fff;
        transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  }
  
}
